import myEventBus from '@/assets/js/bus';
import { toRefs, reactive, onMounted } from 'vue';
export default {
  name: 'hudongnav',
  setup() {
    const emitter = myEventBus();
    const data = reactive({
      num: 0,
      hudongnav: [{
        id: 'H001',
        title: '对我感兴趣'
      }, {
        id: 'H002',
        title: '看过我'
      }, {
        id: 'H003',
        title: '新职位'
      }]
    });
    const hudongnavclick = i => {
      data.num = i;
      emitter.emit('Tabs', i);
    };
    onMounted(() => {
      emitter.on('slideTab', realIndex => {
        data.num = realIndex;
      });
    });
    return {
      ...toRefs(data),
      hudongnavclick
    };
  }
};